/**
 * Andre måter å mocke ut pdfer er problematiske så derfor beholder vi denne men prøver å gjøre sånn at
 * base64-innholdet ikke blir med i prod-bygg.
 *
 * Alternativene;
 * - fetch-mock, fungerer ikke da browseren ikke nødvendigvis bruker `fetch` for henting av filinnhold
 * - devServerHosting, fungerer greit ved lokal kjøring men vil ikke fungere på heroku (må evt utvide nginx-oppsettet)
 *
 */
export function getMockableUrl(url: string): string {
    if (process.env.REACT_APP_MOCK_ENABLED === 'true') {
        return 'data:application/pdf;base64,';
    }
    return url;
}
